	module.exports = {"data":{"markdownRemark":{"html":"<h2>Color and Culture in Visual Design</h2>\n<p>Color contributes to and affects context.\nFeng shui accepts this principle, whether a contemporary home decor strategy of the West, or as part of a traditional Chinese practice.\nFrom this, who could disagree with the premise that culture and psychology affect how colors are interpreted in context of information?\nSpecific combinations of colors may also represent symbols, as in colors of a national flag, a favorite sports team, school or institution for example.</p>\n<p>Colors are one of the first impressions a viewer recognizes and interprets in communication —before letters are read, numbers analyzed, and graphs interpreted.\nThis is why it is important to research about <a href=\"/working-in-tech/unconscious-bias/index.md\">biases that viewers</a> may have.\nA pallet of colors may change the context and meaning of content you publish.</p>\n<p><a href='https://github.com/freecodecamp/guides/blob/master/README.md' target='_blank' rel='nofollow'>This quick style guide will help ensure your pull request gets accepted</a>.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://en.wikipedia.org/wiki/Rainbows_in_culture\">Rainbows in culture (Wikipedia.org)</a></p>\n<p><a href=\"https://en.wikipedia.org/wiki/Category:Color_in_culture\">Color in culture (Wikipedia category)</a></p>\n<p><a href=\"https://en.wikipedia.org/wiki/Color_psychology\">Color and psychology (Wikipedia.org)</a></p>","fields":{"slug":"/visual-design/color-theory/color-and-culture/"},"frontmatter":{"title":"Color and Culture in Visual Design"}}},"pathContext":{"slug":"/visual-design/color-theory/color-and-culture/"}}

